import React from "react";
import { LinkStyled, NavList } from "./show/Navs.styled";

const Navs = () => {
  const LINKS = [
    { to: "/", text: "Home Page" },
    { to: "/starred", text: "Starred Page" },
  ];
  return (
    <div>
      <NavList>
        {LINKS.map((item) => (
          <li key={item.to}>
            <LinkStyled to={item.to}>{item.text}</LinkStyled>
          </li>
        ))}
      </NavList>
    </div>
  );
};

export default Navs;
